#timetable {

    min-width: 178px;
    
    .day {
        padding-right: 0;
    }

    .time {
        text-align: right;
        padding-left: 0;
    }
}

#footer-section {
    
        // background-color: #394255;
        background-color: #fafafa;
        // color: white;
        

        h2, h3 {
            // color: white;
        }

        a {
            color: var(--secondary);
            font-weight: 600;
        }

        .table td, .table th {
            border: none;
            padding: 0.2rem;
        }

        .container {
            // position: relative;
            font-size: 1rem;
        }

        .section-sub-title, .section-title {
            @media (max-width: 560px) {
                text-align: center;
                justify-content: center;                
            }
        }

        #footer-copyright {
            p {
                padding-bottom: 0.5rem;
                margin-bottom: inherit;
                font-size: 10px;
                // color: white;
                text-align: center;
            }
        }
}

